<script>
    import Remote from "../../remote/scheduleRequest"

    export default {
        name: 'scheduleNavigation',
        data() {
            return {
                user: null,
                menus: [
                    {
                        text: 'Work Schedule',
                        display: 'My Schedules',
                        route: "/my-schedules",
                        selected: false,
                        default: true
                    },
                    {
                        text: 'Schedule Management',
                        display: 'Team Schedules',
                        route: "/team-schedules",
                        selected: false,
                        default: false,
                    },
                    {
                        text: 'Employee Allocation',
                        display: 'Employee Allocations',
                        route: "/employee-allocations",
                        selected: false,
                        default: false
                    }
                ],
                selectedMenu: null
            }
        },
        computed: {
            userFullName() {
                return this.user ? `${this.user.FirstName} ${this.user.LastName}` : '';
            },
            authorizedMenus() {
                var filteredMenus = [];
                var accessibleMenus = this.user.AccessibleMenus;
                for (var i = 0; i < this.menus.length; i++) {
                    var currentMenuIteration = this.menus[i];
                    if (accessibleMenus.indexOf(currentMenuIteration.text) != -1) {
                        filteredMenus.push(currentMenuIteration);
                    }
                }
                return filteredMenus;
            }
        },
        methods: {
            selectMenu: function (menu) {
                this.$router.push(menu.route)

                this.menus.forEach(m => {
                    m.selected = false;
                })
                menu.selected = true


            },
            isSelected: function (menuText) {
                return this.selectedMenu && this.selectedMenu.text == menuText;
            }
        },
        created: function () {

            // PLEASE DO NOT MODIFY "GAReady" EVENT DISPATCHERS BELOW
            // If you MUST move or remove this, you MUST dispatch "GAReady" whenever this app has an authorised user with a valid companyNumber and employeeNumber.
            // This is vital for Affinity Management Analytics reporting. 

            if (!window.Affinity || (window.Affinity && !window.Affinity.user)) {
                console.log('no affinity requesting authInfo')
                // call request to get user
                Remote.AuthGetInfo().then(res => {
                    this.user = res;

                    try
                    {
                        window.dispatchEvent(new CustomEvent('GAReady', { bubbles: true, detail: { companyNumber: this.user.CompanyNo, employeeNumber: this.user.EmployeeNo, appVersion: 'V1' } }));
                    }
                    catch(err)
                    {
                        console.warn('GA tracking failed. Contact BenK.');
                    }

                });
            } else {
                console.log('loading user from Affinity lib')
                this.user = window.Affinity.user;

                try
                {
                    window.dispatchEvent(new CustomEvent('GAReady', { bubbles: true, detail: { companyNumber: this.user.CompanyNo, employeeNumber: this.user.EmployeeNo, appVersion: 'V1' } }));
                }
                catch(err)
                {
                  console.warn('GA tracking failed. Contact BenK.');
                }

                // todo: move this logic to router
                let selected = this.menus.find(m => m.route === this.$route.path)

                if (!selected) {
                    let defaultMenu = this.menus.find((menu => {
                        return menu.default === true;
                    }))
                    this.selectMenu(defaultMenu)
                }
            }

        },
    }
</script>


<template>
    <div class="schedules">
        <div class="sch-nav justify-space-between align-center pl-2 primary body-2">
            <div class="white--text">Schedules - {{ userFullName }}</div>
            <div class="d-flex align-center" v-if="user">
                <ul class="sch-nav-options mr-1 pa-0">
                    <li class="nav-box mr-1 py-1 px-2 my-1 caption" v-for="menu in authorizedMenus"
                        @click="selectMenu(menu)" :class="{ selected : menu.selected }">
                        <div>{{menu.display}}</div>
                    </li>
                </ul>
                <!--  todo: data-tooltip="Help" data-tooltip-dir="bottom,left"-->
                <a href="/help/index" class="sch-nav-help align-center mr-2" v-tooltip.top="{content:'Help'}" target="_blank">
                    <v-icon color="white" size="25">icon-help-round</v-icon>
                </a>
            </div>

        </div>
    </div>
</template>
<style lang="scss" scoped>
    .nav-box {
        border-bottom: 1px solid #44b5ec;
        background-color: #2d72b3;
        color: white;
        list-style-type: none;
        cursor: pointer;
    }

    .selected {
        background-color: #448ccf;
    }

    .sch-nav {
        display: flex;
        padding: 5px 0;

        @media only screen and (max-width: 768px) {
            display: block;


        }
    }

    .sch-nav-options {
        display: flex;
        @media only screen and (max-width: 600px) {
            display: block;
            width: 100%;

        }
    }

    .sch-nav-help {
        display: flex;
        border-radius: 50%;
        width: 30px;
        height: 30px;
        @media only screen and (max-width: 600px) {
            display: none;
        }
    }
</style>