<template>
    <div class="emp-workSched-card pa-4">
        <div class="d-flex justify-space-between">
            <h3>{{selectedEmp.EmployeeName}} ({{selectedEmp.EmployeeNo}})</h3>
            <v-icon @click="closeEmployeeSchedule(true)">icon-cross</v-icon>
        </div>

        <div class="d-flex justify-end align-center mt-4 caption">
            <input class="override-width mr-2" type="checkbox" v-model="activeOnly" @change="updateActiveOnly" v-bind:true-value="1" v-bind:false-value="0">
            <label class="default-line-height">Active Only</label>
            <!--span class="button blue w-icon-only" @click="openSchedAllocation()" v-bind:class="{ 'disabled': showSchedAllocation}" v-show="!bulkAllocationSelected()"><span class="icon-schedule"></span><span>Allocate</span></span-->
        </div>
        <div v-if="empSchedList.length > 0" class="section-table aff-table mt-2">
            <table>
                <thead>
                <tr>
                    <th>Schedule Name</th>
                    <th class="text-center">Date From</th>
                    <th class="text-center">Date To</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="empSched in empSchedList" v-bind:class="empSched.classObject"
                    @click="selectEmpSchedCheck(empSched)">
                    <td>{{empSched.WorkSchedName}}</td>
                    <td class="text-center">{{empSched.DateFrom | date}}</td>
                    <td class="text-center">{{empSched.DateTo | date}}</td>
                </tr>
                </tbody>
            </table>
        </div>
        <div v-else> Work schedule empty.</div>

        <template v-if="selectedEmpSched">
            <emp-schedule-detail ref="empScheDetailRef" :empSched="selectedEmpSched" @deleted="deleteSelection" @updated="updatedEmpSchedule" ></emp-schedule-detail>
        </template>
    </div>

</template>

<script>
    import {bus} from "../../main";
    import Remote from "../../remote/scheduleRequest";
    import EmpScheduleDetail from "./empSchedDetail1";
    import Dialog from "../dialog";

    export default {
        name: "employeeSchedule",
        components: {EmpScheduleDetail},
        props: ['employee'],
        data: function () {
            return {
                selectedEmp: null,
                empSchedList: [],
                activeOnly: false,
                selectedEmpSched: null
            }
        },
        created() {
            this.selectedEmp = this.employee;
            if (this.selectedEmp && this.selectedEmp.EmployeeNo) {
                this.getEmpWorkScheds()
            } else {
                console.error('employee or employee number missing')
            }
        },
        watch: {
            employee() {
                this.selectedEmp = this.employee;
                if (this.selectedEmp && this.selectedEmp.EmployeeNo) {
                    this.getEmpWorkScheds()
                } else {
                    console.error('employee or employee number missing')
                }
            }
        },
        methods : {
            selectEmpSchedCheck(empSched) {
                if (this.$refs.empScheDetailRef && this.$refs.empScheDetailRef.hasChanges) {
                    Dialog.checkChange({
                        message: 'There are currently Schedule changes <br /> Do you want to discard your changes?',
                        onOk: () => {
                            this.selectEmpSched(empSched)
                        }
                    })
                } else {
                    this.selectEmpSched(empSched)
                }
            },
            selectEmpSched(empSched) {

                this.empSchedList.forEach(empSched => {
                    empSched.classObject.selected = false;
                })

                empSched.classObject.selected = true
                this.selectedEmpSched = empSched;
            },

            getEmpWorkScheds() {
                Dialog.loading()
                Remote.EmpWorkScheds(this.selectedEmp.EmployeeNo + (this.activeOnly ? "?activeOnly=true" : "?activeOnly=false"))
                    .then(res => {
                        res.Data.map((empSched, index) => {
                            empSched.classObject = {
                                'selected': false,
                                'odd': index % 2
                            }
                            empSched.DateFrom = new Date(Date.parse(empSched.DateFrom));
                            if (empSched.DateTo)
                                empSched.DateTo = new Date(Date.parse(empSched.DateTo));
                        });
                        this.empSchedList = res.Data;
                        Dialog.clear();
                    })
            },

            updateActiveOnly() {
                this.getEmpWorkScheds();
            },
            closeEmployeeSchedule(changeCheck) {
                if (!changeCheck || !this.$refs.empScheDetailRef || (this.$refs.empScheDetailRef && !this.$refs.empScheDetailRef.hasChanges)) {
                    this.$emit('close')
                    return;
                }

                Dialog.checkChange({
                    message: 'There are currently Schedule changes <br /> Do you want to discard your changes?',
                    onOk: () => {
                        this.closeEmployeeSchedule(false)
                    }
                })
            },
            deleteSelection() {
                this.selectedEmpSched = null;
                this.getEmpWorkScheds();
            },
            updatedEmpSchedule() {
                this.selectedEmpSched = null;
                this.getEmpWorkScheds();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .emp-workSched-card {
        display: inline-block;
        vertical-align: top;
        min-height: 450px;
        background: #efefef;
        width: 460px;
        /*font-size: 12px;*/

        @media only screen and (max-width: 980px) {
            margin-top: 10px;

        }

        @media only screen and (max-width: 600px) {
            width: 100%;
            margin-top: 8px;
        }
    }

    .aff-table {
        tr {
            cursor: pointer;
        }
    }

    .override-width {
        width: auto !important;
    }
</style>