<template>
    <div id="sched-man-list " class="sched-man-list"  v-show="!createNew">
        <div>
            <div class="d-flex align-center flex-wrap">
                <v-text-field v-model="filters.searchTerm" type="text" placeholder="Search for schedules" hide-details
                              class="sched-search-box body-2"/>
                <div class="filter-dropdown">
                    <v-select class="caption"
                              :menu-props="{bottom: true, offsetY: true}"
                              v-model="filters.selected" :items="filters.options" hide-details></v-select>
                </div>
                <v-btn class="white--text blue ml-2" small @click="createNewSchedule()">
                    <v-icon small>icon-plus</v-icon>
                    <span class="font-weight-regular text-capitalize">&nbsp;Create</span>
                </v-btn>
            </div>
        </div>
        <div class="aff-table sche-search-table mt-4">
            <table class="ui-grid">
                <thead>
                    <tr>
                        <th style="width: 120px" @click="updateSort(0)">Name <span v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[0] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[0] === 'Desc'}"></span> </th>
                        <th @click="updateSort(1)">Description <span v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[1] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[1] === 'Desc'}"></span></th>
                        <th style="width: 70px" class="text-center" @click="updateSort(2)">Weeks <span v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[2] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[2] === 'Desc'}"></span></th>
                        <th style="width: 60px" class="text-center" @click="updateSort(3)">H/W <span v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[3] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[3] === 'Desc'}"></span></th>
                        <th style="width: 80px" class="text-center" @click="updateSort(4)">Allocated<span v-bind:class="{'icon-arrow-line-small-down': filters.orderByArrow[4] === 'Asc', 'icon-arrow-line-small-up': filters.orderByArrow[4] === 'Desc'}"></span></th>
                    </tr>
                </thead>
                <tbody>
                <tr v-for="sched in schedList" @click="selectSchedulesDetail(sched)"
                    v-bind:class="sched.classObject">
                    <td style="width: 50px;" >
                        <div>{{sched.Name}}</div>
                    </td>
                    <td style="width: 100px;"
                        v-tooltip.right="{content: sched.Description, delay: {show: 3000, hide: 100}}">
<!--                        v-tooltip.right="{content: sched.Description,-->
<!--                        class: 'tooltip-custom tooltip-other-custom'}"-->
                        <div class="sched-search-desc">
                            {{sched.Description}}
                        </div>
                    </td>
                    <td class="text-center">
                        <div>{{sched.NoOfWeeks}}</div>
                    </td>
                    <td class="text-center">
                        <div>{{sched.HoursPerWeek}}</div>
                    </td>
                    <td class="text-center">
                        <div>{{sched.Count}}</div>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    import Vue from "vue";
    import Remote from "../../remote/scheduleRequest";
    import moment from "moment";
    import Dialog from '../dialog'

    export default {
        name: "SearchSchedule",
        data () {
            return {
                selectedSched: null,
                schedList: [],
                shiftTypeList: [],
                schedDetail: null,
                createNew: false,
                empListHeight: '',
                showFilters: true,
                // checkboxes

                filters: {
                    searchTerm: '',
                    sort: 'Name',
                    sortOptions: ['Name', 'Description', 'NoOfWeeks', 'HoursPerWeek','Count'],
                    orderBy: 'Asc',
                    orderByArrow: ['Asc','Asc','Asc','Asc','Asc'],

                    partTimeSche: false,
                    fullTimeSche: true,
                    activeSche: true,
                    inActiveSche: false,
                    archivedSche: false,
                    options: ['Show All', 'Show Active', 'Show Inactive', 'Show Archived'],
                    selected: 'Show Active',
                    all: false,
                },
                searchTermFocus: false
            }
        },
        created: function () {
            this.getSchedulesList();

            console.log('schedules management created');
        },
        mounted: function () {
            this.showFilters = true;
            this.$watch('filters', this.refreshFilters, {deep: true});

            console.log('schedules ready created');
        },
        methods: {
            createNewSchedule () {
                this.$emit('createNewSchedule')
            },
            selectSchedulesDetail(sched) {
                this.schedList.map((sched) => {
                    sched.classObject.selected = false;
                })
                sched.classObject.selected = true;
                this.selectedSched = sched;
                this.$emit('selectSchedFromSearch', this.selectedSched)
            },
            deselectScheDetail() {
                this.schedList.forEach((sched) => {
                    sched.classObject.selected = false;
                })
                this.selectedSched = null;
            },

            toggleFilters: function () {
                this.showFilters = !this.showFilters;
            },
            refreshFilters: function () {

                //Sort
                var vm = this;

                this.schedList.sort(function (x, y) {
                    var order = 1;

                    if (vm.filters.orderBy === 'Desc')
                        order = -1;
                    var xString
                    var yString
                    if (typeof x[vm.filters.sort] === 'String') {
                        xString = x[vm.filters.sort].toLowerCase();
                        yString = y[vm.filters.sort].toLowerCase();
                    } else {
                        xString = x[vm.filters.sort];
                        yString = y[vm.filters.sort];
                    }


                    if (xString < yString) {
                        return -1 * order;
                    } else if (xString > yString) {
                        return 1 * order;
                    } else {
                        return 0;
                    }
                })

                var searchTerm = this.filters.searchTerm.toLowerCase().trim();
                this.schedList.forEach(sched => {
                    sched.classObject.selected = vm.selectedSched ? vm.selectedSched.Id == sched.Id : false;
                    sched.classObject.visible = true;

                    // if all filter are selected
                    if (this.filters.all) {
                        sched.classObject.visible = true
                    } else {
                        //Type filter
                        // sched.classObject.visible = this.getCheckboxFilterResult(sched);
                        sched.classObject.visible = this.getDropDownFilterResult(sched);
                        //search term
                        if (sched.classObject.visible && searchTerm.length != 0) {
                            sched.classObject.visible = (sched.Name.toLowerCase().indexOf(searchTerm) !== -1
                                || sched.Description.toLowerCase().indexOf(searchTerm) !== -1) && sched.classObject.visible;
                        }
                    }

                    sched.classObject.hidden = !sched.classObject.visible;
                });

                console.log('sched refresh filter', this.schedList)
            },

            getDropDownFilterResult(sched){
                // All', 'Active', 'InActive', 'Archived'
                switch (this.filters.selected) {
                    case 'Show All':
                        return true;
                    case 'Show Active':
                        return sched.isActive === true && sched.Archive === false;
                    case 'Show Inactive':
                        return sched.isActive === false && sched.Archive === false;
                    case 'Show Archived':
                        return sched.Archive === true;
                }
            },

            getSchedulesList: function () {
                Dialog.loading()
                Remote.StandardSchedules().then(response => {
                    console.log('response', response)
                    var weeks = [];

                    response.Data.map((sched) => {
                        sched.classObject = {
                            selected: false,
                            visible: true,
                            hidden: false
                        };

                        if (this.selectedSched && this.selectedSched.Id === sched.Id) {

                            this.selectedSched = sched;
                            sched.classObject.selected = true;
                        }

                        weeks.push(sched.NoOfWeeks)

                    })
                    weeks.unshift('All');
                    this.schedList = response.Data;
                    this.refreshFilters();
                    Dialog.clear();
                })
            },
            updateSort: function(sortIndex) {
                this.filters.orderBy = this.filters.orderByArrow[sortIndex] = this.filters.orderByArrow[sortIndex] === 'Asc' ? 'Desc' : 'Asc';
                this.filters.sort = this.filters.sortOptions[sortIndex];
                // reset other arrows;
                this.filters.orderByArrow.forEach((arrow, index) => {
                    if (index !== sortIndex) {
                        arrow = 'Asc'
                    }
                })
            },

        }
    }
</script>

<style scoped lang="scss">
    .sched-man-list {
        max-height: 400px;
        max-height: 80vh;
        min-height: 100px;

        .section-table {
            height: 90%;
            padding-bottom:20px;
            overflow: auto;
        }

        @media screen and (max-width: 1200px) {
            /*height: 200px;*/
        }
    }

    .sched-search-box {
        margin-right: 16px;
        @media screen and (max-width: 600px) {
            width: 100%;
            margin-right: 0;
        }
    }

    .emp-sched-list {
        margin-bottom: 20px;
        margin-right: 30px;
        overflow-y: auto;
        max-height:300px;
        min-height:140px;
    }

    .sched-man-filters {
        margin: 0 10px 10px 0;

        .label-width-120 {
            display: inline-block;
            width: 120px;
        }

        .label-width-70 {
            display: inline-block;
            width: 70px;
        }

        .v-input {
            display: inline-flex;
            font-size: 12px;
        }

        span {
            margin-right: 5px;
        }

        select {
            width: auto;
            padding: 2px 5px;
        }
    }

    .sche-search-table {
        overflow: auto;
        max-height: 600px;

        table {
            min-width: 530px;
            table-layout: fixed;

            tr {
                cursor: pointer;
            }

            td {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        @media screen and (max-width: 600px) {
            max-height: 230px;
        }
    }

    .sched-search-desc {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
        line-height: 1;
        vertical-align: middle;
    }

    .filter-dropdown {
        width: 120px;
    }

</style>