import {bus} from "../main";

const DIALOG_QUEUE = []

function testDialog() {
    bus.$emit('dialog', {
        message: 'here is a test dialog',
        showButtons: true,
        showCancel: true,
        okColor: 'orange',
    })
}

function checkChange (input) {
    bus.$emit('dialog', {
        dialog:  true,
        message:  input.message,
        showCancel:  true,
        showButtons:  true,
        showLoader:  false,
        confirmButtonText: input.confirmButtonText ? input.confirmButtonText : 'Yes',
        cancelButtonText: input.cancelButtonText ? input.cancelButtonText : 'No',
        onOk: () => {
            input.onOk();
        },
        onCancel: () => {
            if (input.onCancel) {
                input.onCancel();
            }
        },
    })
}

function alert (message) {
    bus.$emit('dialog', {
        dialog: true,
        message: message,
        showLoader: false,
        showButtons: true,
        onOk: () => {
           // do nothing
        },
        onCancel: () => {
            // do nothing
        }
    })

}

function loading(message) {
    bus.$emit('dialog',{
        dialog: true,
        message: message ? message : "Loading",
        showCancel: false,
        showButtons: false,
        showLoader: true,
    })
    // DIALOG_QUEUE.push('message')
}

function clear() {
    // DIALOG_QUEUE.pop();
    // if (DIALOG_QUEUE.length === 0) {
    bus.$emit('clearDialog')
    // }
}

export default {
    testDialog,
    loading,
    clear,
    checkChange,
    alert
}