<template>
    <div class="section shadow sched-card-default pa-4">
        <h3>Select Employees</h3>
        <v-text-field class="mt-2 caption" v-model="searchTerm" hide-details dense outlined type="text" placeholder="Search for employees "/>
        <div class="aff-table mt-4">
            <table class="ui-grid">
                <tbody>
                <tr v-for="emp in empList" :class="emp.classObject"  @click="toggleEmployeeSelect(emp)" >
                    <td>
                        <span class="name-text-width">{{emp.EmployeeName}} ({{emp.EmployeeNo}})</span>

                        <span class="current-schedules-width">
                            <div class="schedule-name" v-for="name in emp.WorkschedNames">{{name}}</div>
                            <div class="schedule-name">{{emp.PersonalSchedule}}</div>
                        </span>
                    </td>
                    <td>
                        <div>
                            <v-btn v-if="!isMobileView && !emp.classObject.selected" small
                                   class="green white--text"
                                   v-tooltip.right="{content: 'Add to Employee Selection List'}"
                                   @click.stop="addEmployee(emp)">
                                <span class="icon-add-to-list"></span>
                            </v-btn>
                        </div>

                        <v-btn v-if="isMobileView && !emp.classObject.selected" small
                               class="green white--text"
                               @click.stop="addEmployee(emp)" >
                            <span class="icon-add-to-list"></span>
                        </v-btn>
                        <v-btn v-if="emp.classObject.selected" small
                               class="grey"
                               v-tooltip.right="{content: 'Remove Employee From Selection List'}"
                               @click.stop="removeEmployee(emp)">
                            <span class="icon-minus white--text"></span>
                        </v-btn>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    import {bus} from "../../main";
    import Remote from "../../remote/scheduleRequest";
    import Vue from "vue";
    import Dialog from '../dialog'

    export default {
        name: "SearchEmployee",
        data: function () {
            return {
                // employee list
                searchTerm: '',
                empList: [],
                selectedEmpList: [],
                selectedEmp: null,
                isMobileView: false,
            }
        },
        mounted() {
            if (navigator.userAgent.match(/Android/i)
                || navigator.userAgent.match(/webOS/i)
                || navigator.userAgent.match(/iPhone/i)
                || navigator.userAgent.match(/iPad/i)
                || navigator.userAgent.match(/iPod/i)
                || navigator.userAgent.match(/BlackBerry/i)
                || navigator.userAgent.match(/Windows Phone/i)
            ) {
                console.log('is mobile view true')
                this.isMobileView = true;
            } else {
                this.isMobileView = false;
            }

            this.getEmployeesList()
        },
        watch: {
            searchTerm: function (newVal, oldVal) {
                if (oldVal != newVal) {
                    if (newVal.trim().length == 0) {
                        this.empList.forEach(function (emp) {
                            emp.classObject.visible = true;
                            emp.classObject.hidden = false;
                        });

                    } else {
                        var searchTerm = newVal.toLowerCase().trim();
                        this.empList.forEach( (emp) => {
                            emp.classObject.visible = (emp.EmployeeName && emp.EmployeeName.toLowerCase().indexOf(searchTerm) > -1) || (emp.EmployeeNo && emp.EmployeeNo.toString().indexOf(searchTerm) > -1);
                            emp.classObject.hidden = !emp.classObject.visible;
                        });
                    }
                }
            }
        },
        methods: {
            getEmployeesList() {
                Dialog.loading()
                Remote.EmployeesList().then(response => {
                    response.Data.map((sched, index) => {
                        sched.classObject = {
                            'visible': true,
                            'hidden': false,
                            'selected': false,
                            'odd': index % 2
                        }
                    })
                    this.empList = response.Data;
                }).finally(() => {
                    Dialog.clear()
                })

            },
            toggleEmployeeSelect: function (emp) {
                if (emp.classObject.selected) {
                    this.removeEmployee(emp)
                } else {
                    this.addEmployee(emp)
                }
            },
            removeEmployee: function (emp) {
                emp.classObject.selected = false;
                this.selectedEmpList.splice(this.selectedEmpList.indexOf(emp), 1);
                this.$emit('updateSelectedEmpList', this.selectedEmpList)
            },

            addEmployee: function (emp) {
                emp.classObject.selected = true;
                this.selectedEmpList.push(emp);
                this.$emit('updateSelectedEmpList', this.selectedEmpList)
            },

            // external
            // called from parent
            clearSelectedEmployeeList: function () {
                this.selectedEmpList.forEach(emp => {
                    emp.classObject.selected = false;
                })

                this.selectedEmpList = [];
                this.$emit('updateSelectedEmpList', this.selectedEmpList)
            },

            // not used
            transferFromSelectedListToEmployeeList: function () {

                let itemsToTransfer = Object.assign([], this.bulkEmpList);
                for (var i = 0; i < itemsToTransfer.length; i++) {
                    this.empList.push(itemsToTransfer[i]);
                }
                this.sortEmployeeList();
                this.bulkEmpList = [];

            },

            sortEmployeeList: function () {
                this.empList.sort(function (a, b) {
                    var employeeA = a.EmployeeName.toLowerCase();
                    var employeeB = b.EmployeeName.toLowerCase();

                    if (employeeA < employeeB) {
                        return -1;
                    }

                    if (employeeB > employeeA) {
                        return 1;
                    }
                    return 0;
                });
            },

        }
    }
</script>

<style lang="scss" scoped>
    .sched-card-default {
        background: #efefef;
        width: 460px;

        @media only screen and (max-width: 600px) {
            width: 100%;
        }
    }

    .aff-table {
        max-height: 600px;
        overflow: auto;
        table {
            border: 1px solid #e3e3e3;
        }

        tr {
            cursor: pointer;
        }

        @media only screen and (max-height: 900px) {
            max-height: 600px;
        }

        @media only screen and (max-height: 800px) {
            max-height: 500px;
        }

        @media only screen and (max-height: 700px) {
            max-height: 400px;
        }

        @media only screen and (max-width: 600px) {
            max-height: 200px;
        }
    }

    .emp-selected-table {
        width: 80%;
    }

    .v-btn:not(.v-btn--round).v-size--small {
        height: 24px;
    }

    .name-text-width {
        display: inline-block;
        width: 200px;
        vertical-align: top;
        @media screen and (max-width: 600px) {
            width: 140px;
        }
    }

    .current-schedules-width {
        display: inline-block;
        width: 130px;
    }

    .schedule-name {
        width: 130px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
</style>