<template>
    <v-dialog v-model="dialog"  content-class="dialog-width">
        <div class="promt-box">
            <div>
                <div class="mr-2 d-inline" v-if="showLoader">
                    <img class="loader-icon" src="data:image/gif;base64,">
                </div>

                <div class="content " v-html="message" ></div>
            </div>

            <div class="promptButtons mt-4" v-if="showButtons">
                <v-btn class="orange white--text" v-bind:class="{'mr-4' : showCancel}" small tabindex="9999998" @click="onOk">
<!--                    <v-icon small>icon-tick</v-icon>-->
                    <span class="label text-capitalize">{{confirmButtonText}}</span>
                </v-btn>
                <span class="">&nbsp;</span>
                <v-btn class="grey white--text" v-if="showCancel" small tabindex="9999999"
                    @click="onCancel">
<!--                    <v-icon class="mr-1" small>icon-cancel</v-icon>-->
                    <span class="label text-capitalize">{{cancelButtonText}}</span>
                </v-btn>
            </div>
        </div>
    </v-dialog>
</template>

<script>
    import {bus} from '../main'
    export default {
        name: "alert",
        data() {
            return {
                dialog: false,
                message: 'some message ',
                showLoader: false,
                showButtons: false,
                showCancel: false,
                confirmButtonText: 'Ok',
                cancelButtonText: 'Cancel',
            }
        },
        created () {


            bus.$on('alert', (message) => {
                console.log('bus alert')
                this.dialog = true;
                this.message = message;
                this.showLoader = false;
                this.showButtons = true;
            })

            bus.$on('dialog--loading--request', (message) => {
                console.log('bus loading request')
                this.dialog = true;
                this.message = message;
                this.showCancel = false;
                this.showButtons = false;
                this.showLoader = true;
            })

            bus.$on('clearDialog', () => {
                console.log('bus clear dialog')
                this.dialog = false;
            });
            bus.$on('dialog',  (inputObj) => {
                console.log('bus on dialog')
                this.dialog = !!inputObj;
                this.message = inputObj.message;
                this.showCancel = inputObj.showCancel;
                this.showButtons = inputObj.showButtons;
                this.showLoader = inputObj.showLoader;
                this.confirmButtonText = inputObj.confirmButtonText ? inputObj.confirmButtonText : 'Ok'
                this.cancelButtonText = inputObj.cancelButtonText ? inputObj.cancelButtonText : 'Cancel'

                if (inputObj.onOk) {
                    this.onOk = () => {
                        inputObj.onOk();
                        this.dialog = false;
                    }
                }

                if (inputObj.onCancel) {
                    this.onCancel = () => {
                        inputObj.onCancel();
                        this.dialog = false;
                    }
                }

            })
        },
        methods: {
            onOk() {
                this.dialog = false;
            },
            onCancel() {
                this.dialog = false;
            },
        }
    }
</script>

<style scoped>

    ::v-deep .dialog-width {
        width: auto;
        max-width: 800px;
    }

    .promt-box {
        padding: 20px 60px;
        font-size: 16px;
        background: #333;
        color: #fff;
        text-align: center;
        line-height: 1.2;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
        -webkit-box-shadow: 0 3px 5px rgba(0,0,0,.5);
        -moz-box-shadow: 0 3px 5px rgba(0,0,0,.5);
        box-shadow: 0 3px 5px rgba(0,0,0,.5);
        z-index: 10000000;
    }


    .content {
        display:inline-block;
        vertical-align: middle;
        text-align: center;
    }

    .loader-icon {
        vertical-align: middle;
    }
</style>