<template>
    <div class="outter-h-scroll">
        <div class="fix-content-width">
            <search-employee class="mr-4 mr-xs-0" ref="SearchEmployeeRef" @updateSelectedEmpList="updateEmpList"></search-employee>
            <div class="emp-detail pa-4 mr-4 mr-xs-0" v-if="bulkEmpList.length > 0">
                <div class="section">
                    <div class="d-flex justify-space-between">
                        <h3>Selected Employees</h3>
                    </div>

                    <div class="emp-selection-list mt-4 white caption">
                        <div v-for="(emp, index) in bulkEmpList" class="d-flex justify-space-between align-center py-1 px-2"
                             v-bind:class="{'emp-select-item-odd': index % 2}">
                            <div>
                            <span class="list-text" v-tooltip.right="{content: 'Remove from Employee Selection List'}">{{emp.EmployeeName}} ({{emp.EmployeeNo}})</span>
                            </div>
                            <div>
                                <v-btn v-if="!isMobileView"
                                       class="blue white--text" small
                                       v-tooltip.right="{ content: 'View Schedule Allocations'}" style="margin-right:3px;"
                                       @click="showScheduleDetail(emp)">
                                    <span class="icon-calendar"></span>
                                </v-btn>

                                <v-btn v-if="!isMobileView" small
                                       class="orange white--text"
                                       v-tooltip.right="{ content: 'Remove from Employee Selection List'}"
                                       @click="removeEmployee(emp)">
                                    <span class="icon-minus"></span>
                                </v-btn>

                                <v-btn v-if="isMobileView" small
                                       class="orange white--text"
                                       @click="removeEmployee(emp)">
                                    <span class="icon-minus"></span>
                                </v-btn>

                                <v-btn v-if="isMobileView" class="blue white--text ml-1" small
                                       style="margin-right:3px;" @click="showScheduleDetail(emp)">
                                    <span class="icon-calendar"></span>
                                </v-btn>
                            </div>
                        </div>
                    </div>
                    <div class="schedules-buttons mt-2 d-flex justify-end">
                        <v-btn v-if="!isMobileView" class="button grey white--text" small
                               @click="clearSelectedEmployee()" v-tooltip.right="{ content: 'Clear Employee Selection List'}">
                            <span class="icon-cancel mr-2 vert-mid"></span><span class="text-capitalize ">Clear</span>
                        </v-btn>

                        <v-btn v-if="isMobileView" class="button grey white--text" small
                               @click="clearSelectedEmployee()">
                            <span class="icon-cancel vert-mid "></span><span class="text-capitalize">Clear</span>
                        </v-btn>
                    </div>
                </div>
                <div>
                    <div class="section mt-4" style="height:100%;">
                        <h3 class="bold-text">Allocate Schedule</h3>
                        <div class="mt-4 allocate-sche-form">
                            <form>
                                <v-autocomplete
                                    class="caption pa-0 ma-0" hide-details
                                    placeholder="Select a schedule"
                                    v-model="schedAllocation.WorkschedId"
                                    item-value="Id" :items="schedList" item-text="Name"
                                    :menu-props="{bottom: true, offsetY: true}"
                                    dense>
                                </v-autocomplete>
                                <div v-if="selectedSched" class="mt-4">
                                    <div class="bold-text">Description</div>
                                    <div class="caption">{{selectedSched.Description}}</div>
                                    <div class="bold-text mt-4">No. of Weeks: <span class="ml-4">{{selectedSched.NoOfWeeks}}</span>
                                    </div>
                                </div>
                                <div class="d-flex justify-space-between">
                                    <div class="mt-4">
                                        <div class="bold-text">Date From</div>

                                        <v-menu v-model="startDateMenu" bottom offsetY
                                                :close-on-content-click="false">
                                            <template v-slot:activator="{ on, attrs }">
                                                <v-text-field :value="schedAllocation.DateFrom | date"
                                                              v-bind="attrs" v-on="on"
                                                              outlined dense hide-details
                                                              autocomplete="off"
                                                              class="d-inline-flex ma-0 pa-0 mr-2 caption white date-input-width"
                                                              append-icon="icon-calendar"
                                                              @change="updateDateText($event, schedAllocation.DateFrom)"
                                                              @click:clear="fromDate = false">
                                                </v-text-field>
                                            </template>
                                            <v-date-picker :value="startDatePickerValue" @input="startDateDatePickerHandler"
                                                           :weekday-format="getDay"
                                                           no-title @change="startDateMenu = false"></v-date-picker>
                                        </v-menu>
                                    </div>
                                    <div class="mt-4">
                                        <div class="bold-text">Date To</div>

                                        <v-menu v-model="toDateMenu" bottom offsetY
                                                :close-on-content-click="false">
                                            <template v-slot:activator="{ on, attrs }">
                                                <v-text-field :value="schedAllocation.DateTo | date"
                                                              v-bind="attrs" v-on="on"
                                                              autocomplete="off"
                                                              outlined dense hide-details
                                                              class="d-inline-flex ma-0 pa-0 caption white date-input-width"
                                                              append-icon="icon-calendar"
                                                              @change="updateDateText($event, schedAllocation.DateTo)"
                                                              @click:clear="toDateMenu = false">
                                                </v-text-field>
                                            </template>
                                            <v-date-picker :value="toDatePickerValue" @input="toDateDatePickerHandler"
                                                           :weekday-format="getDay"
                                                           no-title @change="toDateMenu = false"></v-date-picker>
                                        </v-menu>
                                    </div>
                                </div>

                                <div class="mt-4">
                                    <div class="bold-text">Notes</div>
                                    <v-textarea rows="3" class="white caption" outlined hide-details dense
                                                @keyup="hasChanges = true"
                                                v-model="schedAllocation.Comment"> </v-textarea>
                                </div>
                                <div class="form-row mt-4" v-if="schedAllocation.DateFrom">
                                    <label class="label-width">End existing schedules on {{capDate}}</label>
                                    <input type="checkbox" value="1" v-model="schedAllocation.EndExistingScheds">
                                </div>
                                <div class="schedules-buttons mt-4 d-flex justify-end">
                                    <v-btn class="grey vert-mid" small @click="closeSchedAllocation()">
                                        <span class="icon-cancel white--text mr-1"></span>
                                        <span class="text-capitalize white--text">Cancel</span>
                                    </v-btn>
                                    <v-btn class="green ml-4" small @click="allocateEmployees()">
                                        <span class="icon-paperplane vert-mid white--text mr-1"></span>
                                        <span class="text-capitalize white--text">Allocate</span>
                                    </v-btn>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <template v-if="selectedEmpForDetail">
                <employee-schedule ref="EmployeeScheduleRef" :employee="selectedEmpForDetail" @close="resetSelectedEmpForDetail"></employee-schedule>
            </template>
        </div>
    </div>

</template>
<script>
    import Remote from "../../remote/scheduleRequest";
    import {bus} from "../../main";
    import moment from "moment";
    import EmployeeSchedule from "./employeeSchedule1";
    import SearchEmployee from "./SearchEmployee";
    import Dialog from '../dialog';

    export default {
        name: 'employeeAllocation',
        components: {
            SearchEmployee,
            EmployeeSchedule
        },
        data: function () {
            return {
                bulkEmpList: [],
                schedList: [],
                hasChanges: false,

                // form model
                schedAllocation: {
                    WorkschedId: -1,
                    DateFrom: moment(),
                    DateTo: null,
                    Comment: '',
                    EndExistingScheds: true
                },

                // employee-chedule component input data
                selectedEmpForDetail: null,

                // datePicker
                startDateMenu: false,
                toDateMenu: false,
                startDatePickerValue: '',
                toDatePickerValue: '',

                //
                isMobileView: false,
            };
        },

        created: function () {
            this.isMobileView = this.checkIsMobileView()
            // Dialog.loading()
            Remote.StandardSchedules().then(response => {
                this.schedList = response.Data.filter(data => data.Archive === false );
               //  Dialog.clear()
            })
        },
        mounted: function () {
            this.$watch('schedAllocation', function (newVal, oldVal) {
                if (newVal === oldVal) {
                    this.hasChanges = true; //ignore employee list changes.
                }
            }, {deep: true});
        },
        computed: {
            selectedSched() {
                if (this.schedAllocation.WorkschedId === -1) {
                    return false;
                }

                return this.schedList.find(sched => {
                    return sched.Id === this.schedAllocation.WorkschedId
                })
            },

            capDate: function () {
                if (!this.schedAllocation.DateFrom) {
                    return
                }

                let date = moment(this.schedAllocation.DateFrom);
                let yesterday = date.subtract(1, 'day')
                return yesterday.format('DD/MM/YYYY');
            },
        },

        methods: {
            getDay(date){
                const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

                let i = new Date(date).getDay(date)
                return daysOfWeek[i]
            },
            updateDateText(event, target) {
                this.startDateMenu = false
                this.toDateMenu = false;
                target.set(moment(event, 'DD/MM/YYYY').toObject())
                // todo: add check for is correct format
                this.datePickerFormattedFromDate();
                this.datePickerFormattedToDate();
            },

            datePickerFormattedFromDate() {
                this.startDatePickerValue = this.schedAllocation.DateFrom ? moment(this.schedAllocation.DateFrom).format('YYYY-MM-DD') : undefined; // Date picker objects needs date in this particular format
            },
            datePickerFormattedToDate() {
                this.toDatePickerValue = this.schedAllocation.DateTo ? moment(this.schedAllocation.DateTo).format('YYYY-MM-DD') : undefined; // Date picker objects needs date in this particular format
            },

            updateEmpList(empList) {
                this.bulkEmpList = empList
            },
            startDateDatePickerHandler(date) {
                if (typeof date === 'string')
                    date = moment(date);
                this.schedAllocation.DateFrom = date;
                this.datePickerFormattedFromDate()
            },
            toDateDatePickerHandler(date) {
                if (typeof date === 'string')
                    date = moment(date);
                this.schedAllocation.DateTo = date;
                this.datePickerFormattedToDate()
            },

            removeEmployee: function (emp) {
                this.$refs.SearchEmployeeRef.removeEmployee(emp)
            },

            clearSelectedEmployee: function (emp) {
                if (this.hasChanges) {
                    Dialog.checkChange({
                        message: 'There are currently Schedule Allocation changes <br /> Do you want to discard your changes?',
                        onOk: () => {
                            this.$refs.SearchEmployeeRef.clearSelectedEmployeeList();
                            this.resetSchedAllocation()
                        }
                    })
                } else {
                    this.$refs.SearchEmployeeRef.clearSelectedEmployeeList();
                }
            },

            allocateEmployees: function () {
                var warningMsgs = '';
                if (this.selectedSched.Id < 1 || !this.selectedSched.Id) {
                    warningMsgs += '<br />Select a schedule that you want to allocate.';
                }

                if (this.schedAllocation.DateFrom && this.schedAllocation.DateTo &&
                    this.schedAllocation.DateFrom.diff(this.schedAllocation.DateTo) > 0) {
                    warningMsgs += '<br />Date To cannot be before Date From.';
                }

                if (warningMsgs != '') {
                    bus.$emit('dialog', {
                        message: warningMsgs,
                        showButtons: true,
                        noClose: false,
                        showLoader: false
                    });

                    return;
                }

                var postData = {
                    WorkschedId: this.selectedSched.Id,
                    EmployeeList: [],
                    DateFrom: moment(this.schedAllocation.DateFrom) ? moment(this.schedAllocation.DateFrom).format('DD/MMM/YYYY') : null,
                    DateTo: moment(this.schedAllocation.DateTo) ? moment(this.schedAllocation.DateTo).format('DD/MMM/YYYY') : null,
                    Comment: this.schedAllocation.Comment,
                    EndExistingScheds: this.schedAllocation.EndExistingScheds
                };

                if ( this.bulkEmpList.length > 0) {
                    this.bulkEmpList.forEach((emp) => {
                        postData.EmployeeList.push(emp.EmployeeNo);
                    });
                } else if (this.selectedEmp) {
                    postData.EmployeeList = [this.selectedEmp.EmployeeNo];
                }

                Dialog.loading()
                Remote.AllocateSchedule(postData).then(res => {
                    this.hasChanges = false;
                    this.resetSchedAllocation();
                    Dialog.clear()
                    if (this.$refs.EmployeeScheduleRef) {
                        this.$refs.EmployeeScheduleRef.getEmpWorkScheds()
                    }

                    // this.getEmpWorkScheds
                })
                .catch(err => {
                    Dialog.alert(err.message)
                    console.error('err', err)
                })
            },

            closeSchedAllocation: function () {
                if (this.hasChanges) {
                    Dialog.checkChange({
                        message: 'You have unsaved changes. Are you sure to discard the changes?',
                        onOk: () => {
                            this.resetSchedAllocation();
                            this.hasChanges = false;
                            this.$refs.SearchEmployeeRef.clearSelectedEmployeeList();
                        },
                    })
                } else {
                    this.$refs.SearchEmployeeRef.clearSelectedEmployeeList();
                }
            },

            showScheduleDetail: function (employee) {
                this.selectedEmpForDetail = employee;
            },

            resetSelectedEmpForDetail() {
                this.selectedEmpForDetail = null;
            },

            resetSchedAllocation: function () {
                this.hasChanges = false;
                this.schedAllocation = {
                    WorkschedId: -1,
                    DateFrom: moment(),
                    DateTo: null,
                    Comment: '',
                    EndExistingScheds: true
                };
            },

            checkIsMobileView: function () {
                if (navigator.userAgent.match(/Android/i)
                    || navigator.userAgent.match(/webOS/i)
                    || navigator.userAgent.match(/iPhone/i)
                    || navigator.userAgent.match(/iPad/i)
                    || navigator.userAgent.match(/iPod/i)
                    || navigator.userAgent.match(/BlackBerry/i)
                    || navigator.userAgent.match(/Windows Phone/i)
                ) {
                    return true;
                } else {
                    return false;
                }
            },
        },
    }
</script>


<style scoped lang="scss">
    $button-blue: #44b5ec;
    $table-odd-color: #f6f6f6;

    .outter-h-scroll {
        overflow-x: auto;
    }

    .fix-content-width {
        width: max-content;

        @media only screen and (max-width: 980px ) {
            width: auto;
            text-align: center;
        }
    }


    .vert-mid {
        vertical-align: middle;
    }

    .emp-detail {
        display: inline-block;
        width: 380px;
        background: #efefef;

        @media only screen and (max-width: 980px) {
            margin-top: 10px;
        }

        @media only screen and (max-width: 600px) {
            margin-top: 8px;
            width: 100%;
        }
    }

    .emp-selection-list {
        max-height: 150px;
        overflow: auto;
        broder: 1px solid #e3e3e3;
    }

    .emp-select-item-odd {
        background-color: $table-odd-color;
    }

    .aff-table table tr .emp-selected {
        background: $button-blue;
        color: white;
    }

    textarea {
        width: 100%;
        padding: 5px;

        margin: 0;
        border-top: 1px solid #b3b3b3;
        border-right: 1px solid #e6e6e6;
        border-bottom: 1px solid #fff;
        border-left: 1px solid #e6e6e6;
        font-size: 12px;
        font-family: 'Montserrat', Arial, 'Helvetica Neue', Helvetica, Sans-Serif;
        background: #fff;
    }

    .allocate-sche-form {
        font-size: 12px;
    }

    .date-input-width {
        max-width: 160px;
    }
</style>