import Vue from 'vue'
import VueRouter from 'vue-router'
import mySchedules from "../components/mySchedules/mySchedules1";
import schedulesManagement from "../components/schedulesManagement/schedulesManagement1";
import employeeAllocation from "../components/employeeAllocation/employeeAllocation1";

Vue.use(VueRouter)

const routes = [
    {
        path: '/my-schedules',
        name: 'My Schedule',
        component: mySchedules
    },

    {
        path: '/team-schedules',
        name: 'Schedule Management',
        component: schedulesManagement
    },

    {
        path: '/employee-allocations',
        name: 'Employee Allocation',
        component: employeeAllocation
    },
    {
        path: '/',
        name: 'Home',
        component: mySchedules
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
