<template>
	<div class="default-form sched-detail-form mt-4">
		<form @change="formUpdated">
			<div class="caption d-flex justify-space-between">
				<div class="full-width d-flex align-center">
					<div class="form-label">Name</div>
					<v-text-field
						class="caption"
						name="name"
						@keyup="hasChanges = true"
						:rules="nameLengthRule"
						counter="49"
						v-if="isNewSchedule"
						v-model="detail.Name"
					></v-text-field>
					<div
						v-else
						class="label-detail"
						v-tooltip.top="{
							content: detail.Name,
							delay: { show: 3000, hide: 100 },
						}"
					>
						{{ detail.Name }}
					</div>
				</div>
				<div v-if="detail.Archive" class="red--text">Archived</div>
			</div>
			<div class="caption d-flex mt-4">
				<div class="form-label">Description</div>
				<v-textarea
					class="white caption"
					hide-details
					rows="2"
					@keyup="hasChanges = true"
					outlined
					dense
					v-model="detail.Description"
				>
				</v-textarea>
			</div>
			<div class="caption mt-4">
				<div class="form-label">Effective Date</div>
				<v-menu
					v-model="startDateMenu"
					bottom
					offsetY
					:close-on-content-click="false"
				>
					<template v-slot:activator="{ on, attrs }">
						<v-text-field
							:value="detail.StartDate | date"
							v-bind="attrs"
							v-on="on"
							name="Effective Date"
							outlined
							dense
							hide-details
							@change="updateDateText($event, detail.StartDate)"
							class="d-inline-flex ma-0 pa-0 mr-2 caption white"
							style="max-width: 120px"
							@click:clear="fromDate = null"
						>
						</v-text-field>
					</template>
					<v-date-picker
						:value="datePickerDate"
						@input="startDateDatePickerHandler"
						:weekday-format="getDay"
						no-title
						@change="
							startDateMenu = false;
							hasChanges = true;
						"
					></v-date-picker>
				</v-menu>

				<div class="caption mt-4 ml-4 d-inline number-of-weeks">
					<div class="d-inline mr-4 bold-text">Number of Weeks</div>
					<v-text-field
						dense
						outlined
						hide-details
						type="number"
						class="label-detail white caption"
						max="12"
						min="1"
						step="1"
						style="width: 60px"
						v-model="detail.NoOfWeeks"
						@keydown="numLimitKeyDown"
						@change="updateNoOfWeeks"
					/>
					<div class="mobile-arrow">
						<v-btn
							icon
							v-if="detail.NoOfWeeks < 12"
							small
							color="grey"
							@click="increaseNumber"
						>
							<v-icon>icon-arrowhead-up</v-icon>
						</v-btn>
						<v-btn
							icon
							v-if="detail.NoOfWeeks > 1"
							small
							color="grey"
							@click="reduceNumber"
						>
							<v-icon class="mt-1">icon-arrowhead-down</v-icon>
						</v-btn>
					</div>
				</div>
			</div>

			<div class="section-table ui-gridbox-scrollable-titles mt-4">
				<div class="aff-table sched-detail-table">
					<table
						class="sched-detail-days"
						v-bind:class="{ 'shift-min-width': showShift }"
					>
						<thead>
							<tr>
								<th>Week</th>
								<th>Day</th>
								<th>Hours</th>
								<th>Start</th>
								<th>Finish</th>
								<th v-show="showShift">Shift</th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="d in detail.Days" :key="d.DayNumber">
								<td>
									<span
										v-show="isFirstDayOfWeek(d.DayOfWeek)"
										>{{ d.Week }}</span
									>
								</td>
								<td class="table-date-counter">
									<span class="mr-2">{{
										d.DayNumber + 1
									}}</span>
									<span>{{ getDayOfWeek(d.DayOfWeek) }}</span>
								</td>
								<td>
									<v-text-field
										type="number"
										step="any"
										min="0"
										max="24"
										v-model.number="d.Hours"
										class="caption"
										center
										name="Sche Hour"
										outlined
										dense
										hide-details
										autocomplete="off"
										:error="d.error"
										@keydown="
											numLimitKeyDown($event, true, false)
										"
										@change="checkHour(d)"
										:data-test="d.Hours"
									></v-text-field>
								</td>
								<td>
									<v-text-field
										:value="d.StartTime | time"
										name="Sche StartTime"
										outlined
										dense
										hide-details
										autocomplete="off"
										class="d-inline-flex ma-0 pa-0 caption white"
										@change="
											timeTextChanged(
												$event,
												d,
												'StartTime',
											)
										"
										@keydown="
											numLimitKeyDown($event, false, true)
										"
										@click:clear="fromDate = null"
									>
									</v-text-field>
								</td>
								<td>
									<v-text-field
										:value="d.FinishTime | time"
										name="Sche FinishTime"
										outlined
										dense
										hide-details
										autocomplete="off"
										class="d-inline-flex ma-0 pa-0 caption white"
										@change="
											timeTextChanged(
												$event,
												d,
												'FinishTime',
											)
										"
										@keydown="
											numLimitKeyDown($event, false, true)
										"
										@click:clear="fromDate = null"
									>
									</v-text-field>
								</td>
								<td class="shift-select-td" v-show="showShift">
									<v-select
										class="caption"
										dense
										outlined
										hide-details
										:menu-props="{
											bottom: true,
											offsetY: true,
										}"
										v-model="d.ShiftId"
										@change="hasChanges = true"
										:items="shifts"
										item-text="name"
										item-value="name"
									></v-select>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>

			<div
				class="schedules-buttons form-row d-flex flex-wrap justify-end mt-2"
			>
				<v-btn
					small
					class="white--text grey caption text-capitalize mr-2 mt-2"
					@click="toggleShift"
				>
					<span v-if="!showShift">Show Shift</span>
					<span v-if="showShift">Hide Shift</span>
				</v-btn>
				<v-btn
					class="white--text red text-capitalize mr-2 mt-2"
					small
					:disabled="hasChanges"
					@click="deleteSchedule()"
					v-if="
						!isNewSchedule &&
						detail.EmpAllocations &&
						detail.EmpAllocations.length === 0
					"
				>
					<span class="icon-trash mr-1"></span><span>Delete</span>
				</v-btn>
				<v-btn
					class="white--text red text-capitalize mr-2 mt-2"
					small
					:disabled="hasChanges"
					@click="archiveSchedule()"
					v-if="!detail.Archive && !isNewSchedule"
				>
					<span class="icon-briefcase mr-1"></span
					><span>Archive</span>
				</v-btn>
				<v-btn
					class="white--text grey text-capitalize mr-2 mt-2"
					small
					:disabled="!hasChanges && !isNewSchedule"
					@click="cancelChanges()"
				>
					<span class="icon-cancel mr-1"></span><span>Cancel</span>
				</v-btn>
				<v-btn
					class="white--text text-capitalize mr-1 mt-2"
					color="green"
					small
					:disabled="!hasChanges"
					@click="updateDetail()"
				>
					<span class="icon-save mr-1"></span><span>Save</span>
				</v-btn>
			</div>
		</form>
	</div>
</template>
<script>
	import Vue from "vue";
	import moment from "moment";
	import Remote from "../../remote/scheduleRequest";
	import { bus } from "../../main";
	import Dialog from "../dialog";

	export default {
		name: "schedule-detail",
		props: ["detail", "shiftTypeList", "readOnly"],
		data: function () {
			return {
				nameLengthRule: [(v) => v.length <= 49 || "Max 50 characters"],
				week: 1,
				tablePadding: "0 0;",
				hasChanges: false,
				isDateToCleared: false,
				shifts: [],
				showShift: true,
				initialEmpSched: null,

				startDateMenu: false,

				//
				datePickerDate: "",

				//  time picker
				timePickerTargetDay: null,
				selectedTimeType: "Start", // Start Finish
			};
		},
		computed: {
			isNewSchedule() {
				return this.detail.Id <= 0;
			},
		},
		created: function () {
			this.loadShiftTypes();
		},

		mounted: function () {
			this.hasChanges = this.detail.Id == -1; //new schedules always haschanges

			this.$emit("dataChanged", this.hasChanges); // todo wtf is components? check what does this line do !!!

			//Add missing 0 days
			this.remapDays();
		},
		watch: {
			["detail"](newVal, oldVal) {
				this.remapDays();

				// todo: simplify this to create event
				if (newVal != oldVal && newVal.Id == -1) {
					this.detail.Days = [];
					this.createNewDays(1, 1);
				}
			},
		},
		methods: {
			increaseNumber() {
				this.detail.NoOfWeeks++;
				this.updateNoOfWeeks();
			},
			reduceNumber() {
				this.detail.NoOfWeeks--;
				this.updateNoOfWeeks();
			},
			toggleShift() {
				this.showShift = !this.showShift;
			},
			getDay(date) {
				const daysOfWeek = [
					"Sun",
					"Mon",
					"Tue",
					"Wed",
					"Thu",
					"Fri",
					"Sat",
				];

				let i = new Date(date).getDay(date);
				return daysOfWeek[i];
			},
			remapDays() {
				var week = 0;
				var date = moment(this.detail.StartDate);
				var total = this.detail.NoOfWeeks * 7;

				var fullDays = new Array(total);

				for (var dayNum = 0; dayNum < total; dayNum++) {
					if (dayNum % 7 == 0) {
						week++;
					}

					fullDays[dayNum] = {
						DayNumber: dayNum,
						DayOfWeek: date.day(),
						Week: week,
						Date: moment(date),
						Hours: 0,
						StartTime: null,
						FinishTime: null,
						ShiftId: null,
					};

					date.add(1, "day");
				}

				this.showShift = false;
				this.detail.Days.map((day) => {
					if (day.StartTime)
						fullDays[day.DayNumber].StartTime = moment(
							day.StartTime,
						);
					if (day.FinishTime)
						fullDays[day.DayNumber].FinishTime = moment(
							day.FinishTime,
						);
					fullDays[day.DayNumber].Hours = day.Hours;
					fullDays[day.DayNumber].ShiftId = day.ShiftId;
				});
				this.detail.Days = fullDays;
				this.detail.StartDate = moment(this.detail.StartDate);
				this.datePickerFormattedDate();
				this.hasChanges = false;
			},

			timeTextChanged(input, d, type) {
				let pickedTime = moment(input, "HH:mm");

				if (type === "FinishTime") {
					if (
						d.FinishTime &&
						pickedTime.hour() === d.FinishTime.hour() &&
						pickedTime.minute() === d.FinishTime.minute()
					) {
						return;
					}

					if (input === "") {
						d.FinishTime = null;
						return;
					}

					d.FinishTime = pickedTime;
				} else {
					if (
						d.StartTime &&
						pickedTime.hour() === d.StartTime.hour() &&
						pickedTime.minute() === d.StartTime.minute()
					) {
						return;
					}

					if (input === "") {
						d.StartTime = null;
						return;
					}

					d.StartTime = pickedTime;
				}

				this.timePickerTargetDay = d;
				this.autoCalcTime(type);

				this.hasChanges = true;
				// this.timeModal = false;
				console.log("timeTextChanged");
			},

			autoCalcTime(type) {
				let targetDay = this.timePickerTargetDay;
				if (
					targetDay.FinishTime &&
					targetDay.FinishTime.format("HH:mm") !== "00:00" &&
					targetDay.StartTime &&
					targetDay.StartTime.format("HH:mm") !== "00:00" &&
					(!targetDay.Hours || targetDay.Hours === 0)
				) {
					// auto calc hours
					// set year month day to same
					targetDay.FinishTime.year(targetDay.StartTime.year());
					targetDay.FinishTime.month(targetDay.StartTime.month());
					targetDay.FinishTime.day(targetDay.StartTime.day());
					let timeDiff = targetDay.FinishTime.startOf("minute").diff(
						targetDay.StartTime.startOf("minute"),
						"hours",
						true,
					);

					targetDay.Hours = Math.round(timeDiff * 100) / 100;
				} else if (
					targetDay.FinishTime &&
					targetDay.Hours &&
					(!targetDay.StartTime ||
						targetDay.StartTime.format("HH:mm") === "00:00")
				) {
					// auto calc start time
					targetDay.StartTime = targetDay.FinishTime.clone().subtract(
						targetDay.Hours,
						"hours",
					);
				} else if (
					targetDay.StartTime &&
					targetDay.Hours &&
					(!targetDay.FinishTime ||
						targetDay.FinishTime.format("HH:mm") === "00:00")
				) {
					// auto calc finish time
					targetDay.FinishTime = targetDay.StartTime.clone().add(
						targetDay.Hours,
						"hours",
					);
				} else if (
					targetDay.FinishTime &&
					targetDay.FinishTime.format("HH:mm") !== "00:00" &&
					targetDay.StartTime &&
					targetDay.StartTime.format("HH:mm") !== "00:00" &&
					(targetDay.Hours || targetDay.Hours !== 0)
				) {
					// otherwise all blank filled.
					console.log("type", type);
					if (type === "StartTime") {
						targetDay.FinishTime = targetDay.StartTime.clone().add(
							targetDay.Hours,
							"hours",
						);
					} else if (type === "FinishTime") {
						targetDay.StartTime =
							targetDay.FinishTime.clone().subtract(
								targetDay.Hours,
								"hours",
							);
					}
				}
			},

			datePickerFormattedDate() {
				this.datePickerDate = this.detail.StartDate
					? moment(this.detail.StartDate).format("YYYY-MM-DD")
					: undefined; // Date picker objects needs date in this particular format
			},

			updateDateText(event, target) {
				this.startDateMenu = false;
				target.set(moment(event, "DD/MM/YYYY").toObject());
				// todo: add check for is correct format
				this.datePickerFormattedDate();
				this.remapDays();
			},

			startDateDatePickerHandler(date) {
				if (typeof date === "string") date = moment(date);
				this.detail.StartDate = date;
				this.datePickerFormattedDate();
				this.remapDays();
			},

			isFirstDayOfWeek: function (dayNo) {
				var startDate = moment(this.detail.StartDate);
				return dayNo == startDate.day();
			},
			loadShiftTypes: function () {
				var list = this.shiftTypeList;

				for (var i = 0; i < list.length; i++) {
					if (
						list[i].ShiftType !== undefined &&
						list[i].ShiftType !== null &&
						list[i].ShiftType.length <= 5
					) {
						var shiftType = {
							name: list[i].ShiftType,
						};
						this.shifts.push(shiftType);
					}
				}

				this.shifts.sort(function (a, b) {
					var x = a.name.toLowerCase();
					var y = b.name.toLowerCase();
					if (x < y) {
						return -1;
					}
					if (x > y) {
						return 1;
					}
					return 0;
				});
			},

			getDayOfWeek: function (dayNo) {
				if (dayNo < 0 || dayNo > 6) {
					console.error("day No not correct");
					return "unknown";
				}

				var days = ["Sun", "Mon", "Tue", "Wed", "Thur", "Fri", "Sat"];
				return days[dayNo];
			},

			checkHour(targetDay) {
				this.hasChanges = true;

				if (targetDay.Hours > 24) {
					Dialog.alert("You can only enter up to 24 hours.");
					targetDay.Hours = 24;
					return;
				}

				console.log("hour", targetDay.Hours);
				if (
					targetDay.StartTime &&
					targetDay.Hours &&
					targetDay.FinishTime
				) {
					targetDay.FinishTime = targetDay.StartTime.clone().add(
						targetDay.Hours,
						"hours",
					);
				} else if (targetDay.StartTime && targetDay.Hours) {
					targetDay.FinishTime = targetDay.StartTime.clone().add(
						targetDay.Hours,
						"hours",
					);
				} else if (targetDay.Hours && targetDay.FinishTime) {
					targetDay.StartTime = targetDay.FinishTime.clone().add(
						targetDay.Hours,
						"hours",
					);
				}
			},

			updateNoOfWeeks() {
				let dayNum = this.detail.Days.length;
				let currentWeek = Math.floor(dayNum / 7);

				if (this.detail.NoOfWeeks > currentWeek) {
					this.createNewDays(currentWeek + 1, this.detail.NoOfWeeks);
				} else {
					let newLength = this.detail.NoOfWeeks * 7;
					this.detail.Days.splice(newLength);
				}
			},

			numLimitKeyDown: function (event, allowDecimal, allowColon) {
				let allow = [8, 13, 9, 46, 16, 17]; // backspace, del, enter, tab, shift, ctrl, dot
				allow.push(65, 67, 86, 88); // a, c, v, x

				if (allowDecimal) allow.push(190, 110); //.

				if (allowColon) allow.push(186);

				if (
					(event.keyCode >= 48 && event.keyCode <= 57) || //key board numbers
					(event.keyCode >= 96 && event.keyCode <= 105) || //key pad numbers
					allow.indexOf(event.keyCode) > -1
				) {
					//various keys
					// all good!
				} else {
					event.preventDefault();
					event.stopPropagation();
				}
			},

			cancelChanges() {
				if (!this.hasChanges) {
					this.$emit("cancel");
					return;
				}

				let msg =
					"You haven't saved your changes.  <br /> Are you sure you want to cancel?";
				Dialog.checkChange({
					message: msg,
					confirmButtonText: "Yes",
					cancelButtonText: "No",
					onOk: () => {
						this.hasChanges = false;
						this.$emit("cancel");
					},
				});
			},

			// todo: use reload to clean the changes
			reloadDetail: function () {
				if (!this.hasChanges) return;
				// toodo: add has change check
				this.$emit("reload");
			},

			createNewDays: function (fromWeek, toWeek) {
				if (fromWeek > toWeek) return;

				var week = fromWeek;
				var dayNum = this.detail.Days.length;
				while (week <= toWeek) {
					var date = moment(this.detail.StartDate);
					date.add(dayNum, "day");
					this.detail.Days.push({
						DayNumber: dayNum,
						DayOfWeek: date.day(),
						Week: week,
						Date: date,
						Hours: 0,
						StartTime: null,
						FinishTime: null,
						ShiftId: null,
						startDateModal: false,
						endDateModal: false,
					});
					dayNum++;
					week = Math.floor(dayNum / 7) + 1;
				}
			},

			archiveSchedule: function () {
				if (this.hasChanges) return;

				if (this.detail.Archive) {
					Dialog.alert(
						"Unable to archive schedule, it's already archived.",
					);
				} else {
					this.detail.Archive = true;
					this.doUpdateDetail();
				}
			},

			deleteSchedule: function () {
				if (this.hasChanges) return;

				if (this.detail.EmpAllocations.length > 0) {
					Dialog.alert(
						"Unable to delete schedule, it's currently allocated to employees",
					);
				} else {
					bus.$emit("dialog", {
						message:
							"Are you sure you want to permanently delete this work schedule?",
						showButtons: true,
						showCancel: true,
						okColor: "red",
						onOk: () => {
							this.doDeleteSchedule();
						},
						onCancel: () => {},
					});
				}
			},

			doDeleteSchedule: function () {
				Dialog.loading();

				Remote.DeleteSchedule(this.detail.Id).then((res) => {
					this.$emit("schedule-deleted");
					Dialog.clear();
				});
			},

			updateDetail: function () {
				if (!this.hasChanges) return;

				if (!this.doUpdateValidation()) {
					return;
				}

				this.doUpdateDetail();
			},
			doUpdateValidation: function () {
				console.log("validation");
				if (
					(!this.detail.Name && !this.detail.Description) ||
					(this.detail.Name === "" && this.detail.Description === "")
				) {
					Dialog.alert(
						"Schedule name cannot be blank. <br /> Schedule description cannot be blank.",
					);
					return false;
				}

				if (!this.detail.Name || this.detail.Name === "") {
					Dialog.alert("Schedule name cannot be blank.");
					return false;
				}

				if (
					!this.detail.Description ||
					this.detail.Description === ""
				) {
					Dialog.alert("Schedule description cannot be blank.");
					return false;
				}

				if (
					this.detail.Name !== undefined &&
					this.detail.Name !== null &&
					this.detail.Name.length > 50
				) {
					Dialog.alert(
						"Schedule names must be 50 characters or fewer (including spaces).",
					);
					return false;
				}

				if (
					this.detail.Description !== undefined &&
					this.detail.Description !== null &&
					this.detail.Description.length > 50
				) {
					Dialog.alert(
						"Schedule descriptions must be 50 characters or fewer (including spaces).",
					);
					return false;
				}

				return true;
			},
			doUpdateDetail: function () {
				var postData = {
					Id: this.detail.Id,
					NoOfWeeks: this.detail.NoOfWeeks,
					StartDate: moment(this.detail.StartDate).format(
						"DD/MMM/YYYY",
					),
					Name: this.detail.Name,
					Description: this.detail.Description,
					Archive: this.detail.Archive,
					Days: [],
				};
				console.log("doUpdateDetail this.detail", this.detail);

				this.detail.Days.map(function (day) {
					if (
						day.Hours > 0 ||
						(day.ShiftId !== null && day.ShiftId !== "")
					) {
						if (day.ShiftId === "") day.ShiftId = null;
						postData.Days.push({
							DayNumber: day.DayNumber,
							StartTime: day.StartTime
								? day.StartTime.format("kk:mm")
								: null,
							FinishTime: day.FinishTime
								? day.FinishTime.format("kk:mm")
								: null,
							Hours: day.Hours > 24 ? 24 : day.Hours,
							ShiftId: day.ShiftId,
						});
					}
				});

				Dialog.loading();
				Remote.PostSchedule(postData)
					.then((res) => {
						this.$emit("schedule-saved", res.Data);
						if (
							this !== undefined &&
							this.hasChanges !== undefined
						) {
							this.hasChanges = false;
						}
						if (this !== undefined) {
							this.hasDataChanges = this.hasChanges;
						}
					})
					.finally(() => {
						Dialog.clear();
					});
			},
			formUpdated() {
				this.hasChanges = true;
			},
		},
	};
</script>

