import Vue from "vue";

Vue.directive('purple', function(el) {
    el.style.color = 'purple'
})

Vue.directive('toolTip',
    function(el, binding, vnode) {
        // TODO: fix required or remove it
        el.addEventListener('mouseenter', function () {
            var $tooltip = document.createElement('div')
            var $tooltipDimension = el.getBoundingClientRect()

            $tooltip.setAttribute('id', 'aff-tooltip')
            if (!binding.value.value) {
                $tooltip.innerHTML = binding.value;
                $tooltip.style.left = $tooltipDimension.left + ($tooltipDimension.width) + 10+ 'px'
                $tooltip.style.top = $tooltipDimension.top + 'px'
                $tooltip.setAttribute('class', 'aff-tooltip-right')
                document.body.appendChild($tooltip)
                return;
            }

            $tooltip.innerHTML = binding.value.value

            let position = 'right';
            if (binding.value.position) {
                position = binding.value.position
            }

            switch (position) {
                // todo: fix left position
                // case 'left':
                //     $tooltip.style.left = $tooltipDimension.left - ($tooltipDimension.width) - 1 + 'px'
                //     $tooltip.style.top = $tooltipDimension.top + 'px'
                //     break;
                case 'right':
                    $tooltip.style.left = $tooltipDimension.left + ($tooltipDimension.width) + 10 + 'px'
                    $tooltip.style.top = $tooltipDimension.top + 'px'
                    $tooltip.setAttribute('class', 'aff-tooltip-right')
                    break;
                case 'top':
                    $tooltip.style.left = $tooltipDimension.left + 'px';
                    $tooltip.style.top = $tooltipDimension.top - 30 - 10 + 'px';
                    $tooltip.setAttribute('class', 'aff-tooltip-top')
                    break;
            }
            if (position === 'left') {

            }


            document.body.appendChild($tooltip)
        })
        el.addEventListener('mouseleave', function () {
            var elemToRemove = document.getElementById('aff-tooltip')
            elemToRemove.parentNode.removeChild(elemToRemove)
        })
    })