<style lang="scss" src="./schedulesManagement.scss"></style>
<template>
    <div class="vue-sched-management">
        <div  class="sched-card-default mr-4 mr-xs-0" v-show="!createNew">
            <div><h3>Schedules</h3></div>
            <search-schedule ref="searchScheduleRef" @createNewSchedule="createNewSchedule" @selectSchedFromSearch="selectSchedulesDetail"></search-schedule>
        </div>
        <div class="sched-card-default sched-detail-card sche-detail-mt" v-if="schedDetail">
            <div v-if="createNew"><h3>Create New Schedule</h3></div>
            <div v-else class="d-flex justify-space-between">
                <h3>Schedule Details</h3>
                <v-icon @click="closeScheduleDetail(true)">icon-cross</v-icon>
            </div>
            <schedule-detail v-if="schedDetail" ref="detail"
                             :shiftTypeList="shiftTypeList"
                             :detail="schedDetail"
                             :read-only="0"
                             @cancel="cancelScheduleDetail"
                             @reload="reloadSchedulesDetail"
                             @schedule-saved="scheduleSaved"
                             @schedule-deleted="scheduleDeleted"></schedule-detail>
            <div class="sched-man-allocations mt-4"
                 v-if="schedDetail && schedDetail.EmpAllocations && schedDetail.EmpAllocations.length > 0">
                <div class="caption font-weight-bold text-start">Employee Allocations</div>
                <div class="aff-table mt-4">
                    <table>
                        <thead>
                        <tr>
                            <th>Employee Name</th>
                            <th class="text-center">Date From</th>
                            <th class="text-center">Date To</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="empSched in schedDetail.EmpAllocations"
                            v-bind:class="{'hide-none-active-detail': !empSched.isActive && showActiveSchedule}">
                            <td>{{empSched.EmployeeName}}</td>
                            <td class="text-center">{{empSched.DateFrom | date}}</td>
                            <td class="text-center">{{empSched.DateTo | date}}</td>
                        </tr>
                        </tbody>
                    </table>
                </div>
                <div class="d-flex justify-end mt-2">
                    <v-btn small class="grey white--text text-capitalize" @click="toggleShowActiveSchedule">
                        <span v-show="!showActiveSchedule">Show Active</span>
                        <span v-show="showActiveSchedule">Show All</span>
                    </v-btn>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
    import moment from 'moment';
    import Remote from '../../remote/scheduleRequest.js'
    import ScheduleDetail from "../scheduleDetail/scheduleDetail1";
    import SearchSchedule from "./SearchSchedule";
    import Dialog from "../dialog";

    export default {
        name: 'schedulesManagement',
        components: {
            SearchSchedule,
            'schedule-detail': ScheduleDetail,
        },
        data() {
            return {
                selectedSched: null,
                shiftTypeList: [],
                schedDetail: null,
                createNew: false,
                empListHeight: '',
                showActiveSchedule: true,
            };
        },
        created() {
            this.getShiftTypeList();
        },
        methods: {
            getShiftTypeList: function () {
                Remote.ShiftTypes().then(res => {
                    this.shiftTypeList = res
                })
            },

            toggleShowActiveSchedule() {
                this.showActiveSchedule = !this.showActiveSchedule;
            },

            selectSchedulesDetail(sched) {
                if (this.$refs.detail && this.$refs.detail.hasChanges) {
                    Dialog.checkChange({
                        message: 'There are currently Schedule Detail changes <br /> Do you want to discard your changes?',
                        onOk: () => {
                            this.loadScheduleDetail(sched)
                        }
                    })
                } else {
                    this.loadScheduleDetail(sched)
                }
            },

            loadScheduleDetail(sched) {
                Dialog.loading()
                Remote.ScheduleDetail(sched.Id).then(res => {
                    this.schedDetail = res.Data
                    this.checkEmpAllocationActive()
                    Dialog.clear();
                    this.selectedSched = sched;
                }, (err) => {
                    Dialog.clear();
                    if (err.message === 'Network Error') {
                        Dialog.alert('Lost Internet connection. Please try again after reconnect.')
                    } else {
                        Dialog.alert('Something went wrong')
                    }
                })
            },

            checkEmpAllocationActive() {
                this.schedDetail.EmpAllocations.forEach(detail => {
                    if (!detail.DateTo) {
                        detail.isActive = true;
                        return
                    }
                    let dateFrom = moment(detail.DateFrom)
                    let dateTo = moment(detail.DateTo)
                    detail.isActive = dateTo.diff(moment(), 'day') >= 0 && dateFrom.diff(moment(), 'day' ) <= 0
                })
            },


            createNewSchedule: function () {
                console.log('create new got called')
                this.createNew = true;
                let startDate = moment();
                startDate.hour(0);
                startDate.minute(0);
                startDate.second(0);
                this.schedDetail = {
                    Id: -1,
                    Name: '',
                    Description: '',
                    NoOfWeeks: 1,
                    StartDate: startDate,
                    Days: []
                };

                if (this.$refs.detail) {
                    this.$refs.detail.hasChanges = true;
                }
            },

            cancelScheduleDetail() {
                console.log('cancel called')
                if (this.createNew) {
                    this.createNew = false;
                }

                this.schedDetail = null;
            },

            reloadSchedulesDetail: function () {
                console.log('reloadSchedulesDetail')
                if (!this.selectedSched)
                    this.schedDetail = null;
                else
                    this.selectSchedulesDetail(this.selectedSched);

                this.createNew = false;
            },

            closeScheduleDetail (changeCheck) {
                if (!changeCheck || !this.$refs.detail.hasChanges) {
                    this.schedDetail = null;
                    this.createNew = false;
                    this.selectedSched = null;
                    this.$refs.searchScheduleRef.deselectScheDetail();
                    return;
                }

                Dialog.checkChange({
                    message: 'There are currently Schedule Detail changes <br /> Do you want to discard your changes?',
                    onOk: () => {
                        this.closeScheduleDetail(false)
                    }
                })
            },

            scheduleSaved: function (detail) {
                if (!this.selectedSched)
                    this.schedDetail = null;
                else {
                    this.schedDetail = detail;
                    this.selectedSched = {
                        Id: detail.Id,
                    };
                }

                this.$refs.searchScheduleRef.getSchedulesList();
                this.createNew = false;
            },

            scheduleDeleted: function () {
                this.selectedSched = null;
                this.schedDetail = null;
                this.$refs.searchScheduleRef.getSchedulesList();
            },
        },

    }
</script>

<style lang="scss" scoped>
    $button-blue: #44b5ec;

    .hide-none-active-detail {
        display: none;
    }

    .sched-card-default {
        background: #efefef;

        @media screen and (max-width: 960px) {
            margin-top: 8px;
        }
    }

    .sched-detail-card {
        width: 565px;

        @media screen and (max-width: 768px) {
            width: 100%;
        }

    }

    .sched-search-box {
        width: 150px;
    }

    .sched-man-allocations {
        display: inline-block;
        vertical-align: top;
        max-width: 600px;
        max-height:400px;
        min-height:200px;
        width:100%;
        padding-right:0px;

        table {
            min-width: 300px;
        }
    }

    .aff-table {
        .selected td {
            background-color: $button-blue;
            color: white;
        }

        .hidden {
            display: none;
        }

        @media screen and (max-width: 768px ) {
            overflow-x: auto;
        }
    }

    .sche-detail-mt {
        @media screen and (max-width: 1440px) {
            margin-top: 10px;
        }
    }
</style>